.main {
  width: 100%;
  margin-top: 2%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.welcome {
  font-size: 1.8rem;
  font-weight: 400;
}

.para {
  font-size: 1.5rem;
  font-weight: 200;
}

@media screen and (max-width: 425px) {
  .welcome,
  .para {
    text-align: center;
  }
}
